.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
}

.modal {
    position: fixed;
    top: 30vh;
    left: 10%;
    width: 80%;
    z-index: 100;
    overflow: hidden;
}

.header {
    background: #4f005f;
    padding: 1rem;
}

.header h2 {
    margin: 0;
    color: white;
}

.content {
    padding: 1rem;
}

.actions {
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
}

.actions button {
    width: 100%;
}

.controls {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.closeButton {
    display: block;
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    border-radius: 1rem;
    z-index: 1000;
    cursor: pointer;
    padding: 1rem 2rem;
}

.error {
    color: white;
    background-color: red;
}

@media (min-width: 768px) {
    .modal {
        left: calc(50% - 20rem);
        width: 40rem;
    }
}